import React, { useState, useEffect, useContext, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import emailjs from "@emailjs/browser";
import { AuthContext } from "../account/AuthContext";
import "./Account.css";

// --- HNG S ---

// Kha lu tr trong localStorage
const LOCAL_STORAGE_KEYS = {
  USERS: "users",
  CURRENT_USER: "currentUser",
};

// Thng bo trng thi
const MESSAGES = {
  EMPTY_FIELDS: "Vui lng nhp y  thng tin!",
  USER_EXISTS: "Tn ng nhp  tn ti!",
  REGISTER_SUCCESS: "ng k thnh cng! Hy ng nhp.",
  LOGIN_SUCCESS: "ng nhp thnh cng!",
  LOGIN_FAILED: "Sai thng tin ng nhp!",
  LOGOUT_SUCCESS: "ng xut thnh cng!",
  CODE_SENT: " gi m xc nhn v email ca bn, vui lng kim tra email.",
  INVALID_CODE: "M xc nhn khng ng!",
  EMPTY_CODE: "Vui lng nhp m xc nhn!",
  MISSING_TEMPLATE_ID: "Li cu hnh: Thiu Template ID cho email xc nhn! Vui lng kim tra file .env.",
  EMPTY_EMAIL: "Vui lng nhp a ch email hp l!",
  PASSWORD_MISMATCH: "Mt khu mi v xc nhn mt khu khng khp!",
  RESET_SUCCESS: "Mt khu  c i thnh cng! Hy ng nhp li.",
  USER_NOT_FOUND: "Khng tm thy ngi dng vi tn ti khon ny hoc email cha c ng k!",
  INVALID_USERNAME: "Tn ti khon khng hp l!",
};

// Thng tin ti khon admin
const ADMIN_CREDENTIALS = {
  USERNAME: "admin",
  PASSWORD: "123",
};

// --- HM TIN CH ---

/**
 * Ly danh sch ngi dng t localStorage
 * @returns {Array} Danh sch ngi dng hoc mng rng nu li
 */
const getStoredUsers = () => {
  try {
    const storedUsers = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEYS.USERS)) || [];
    return Array.isArray(storedUsers) ? storedUsers : [];
  } catch (error) {
    console.error("Li khi c d liu ngi dng t localStorage:", error);
    localStorage.removeItem(LOCAL_STORAGE_KEYS.USERS);
    return [];
  }
};

/**
 * Lu danh sch ngi dng vo localStorage
 * @param {Array} users - Danh sch ngi dng
 */
const saveUsersToStorage = (users) => {
  try {
    localStorage.setItem(LOCAL_STORAGE_KEYS.USERS, JSON.stringify(users));
  } catch (error) {
    console.error("Li khi lu d liu ngi dng vo localStorage:", error);
  }
};

/**
 * To m xc nhn ngu nhin 6 ch s
 * @returns {string} M xc nhn
 */
const generateVerificationCode = () => {
  return Math.floor(100000 + Math.random() * 900000).toString();
};

/**
 * Gi email cha m xc nhn qua EmailJS
 * @param {Object} userData - Thng tin ngi dng (username, email)
 * @param {string} code - M xc nhn
 * @param {Function} setMessage - Hm cp nht thng bo
 */
const sendVerificationEmail = (userData, code, setMessage) => {
  const templateParams = {
    user_name: userData.username || "Ngi dng",
    to_email: userData.email,
    verification_code: code,
  };

  console.log("EmailJS Config:", {
    serviceId: process.env.REACT_APP_EMAILJS_SERVICE_ID,
    templateId: process.env.REACT_APP_EMAILJS_VERIFICATION_TEMPLATE_ID,
    publicKey: process.env.REACT_APP_EMAILJS_PUBLIC_KEY,
  });
  console.log("Template Params:", templateParams);

  if (!templateParams.to_email || !templateParams.to_email.trim()) {
    setMessage(MESSAGES.EMPTY_EMAIL);
    return;
  }

  if (!process.env.REACT_APP_EMAILJS_VERIFICATION_TEMPLATE_ID) {
    setMessage(MESSAGES.MISSING_TEMPLATE_ID);
    return;
  }

  emailjs
    .send(
      process.env.REACT_APP_EMAILJS_SERVICE_ID,
      process.env.REACT_APP_EMAILJS_VERIFICATION_TEMPLATE_ID,
      templateParams,
      process.env.REACT_APP_EMAILJS_PUBLIC_KEY
    )
    .then(
      (response) => {
        console.log("Email xc nhn gi thnh cng:", response.status, response.text);
        setMessage(MESSAGES.CODE_SENT);
      },
      (error) => {
        console.error("Li gi email xc nhn:", error);
        setMessage(`Gi m xc nhn tht bi: ${error.message || "Li khng xc nh"}`);
      }
    );
};

// --- THNH PHN CON ---

/**
 * Form ng nhp hoc ng k
 * @param {Object} props - Props cha thng tin form v cc hm x l
 * @returns {JSX.Element} JSX hin th form
 */
const AuthForm = React.memo(
  ({ formData, isRegistering, onChange, onSubmit, onToggle, message }) => (
    <div className="auth-form">
      <input
        type="text"
        name="username"
        placeholder="Tn ng nhp"
        className="account-input"
        value={formData.username}
        onChange={onChange}
        aria-label="Nhp tn ng nhp"
        autoComplete={isRegistering ? "new-username" : "username"}
      />
      <input
        type="password"
        name="password"
        placeholder="Mt khu"
        className="account-input"
        value={formData.password}
        onChange={onChange}
        aria-label="Nhp mt khu"
        autoComplete={isRegistering ? "new-password" : "current-password"}
      />
      {isRegistering && (
        <input
          type="email"
          name="email"
          placeholder="Email"
          className="account-input"
          value={formData.email}
          onChange={onChange}
          aria-label="Nhp email"
          required
        />
      )}
      {message && (
        <p className={`message ${message.includes("thnh cng") ? "success" : "error"}`}>
          {message}
        </p>
      )}
      <div className="account-buttons">
        {isRegistering ? (
          <>
            <button
              className="account-button register-btn"
              onClick={onSubmit}
              aria-label="ng k ti khon"
            >
              ng k
            </button>
            <button
              className="link-button"
              onClick={onToggle}
              aria-label="Quay li ng nhp"
            >
              Quay li ng nhp
            </button>
          </>
        ) : (
          <>
            <button
              className="account-button login-btn"
              onClick={onSubmit}
              aria-label="ng nhp"
            >
              ng nhp
            </button>
            <button
              className="link-button"
              onClick={() => onToggle(false, true)}
              aria-label="Qun mt khu"
            >
              Qun mt khu?
            </button>
            <button
              className="link-button"
              onClick={() => onToggle(true)}
              aria-label="Chuyn sang ng k"
            >
              Cha c ti khon? ng k
            </button>
          </>
        )}
      </div>
    </div>
  )
);

/**
 * Form nhp username  qun mt khu
 * @param {Object} props - Props cha thng tin form v cc hm x l
 * @returns {JSX.Element} JSX hin th form nhp username
 */
const ForgotPasswordUsernameForm = React.memo(
  ({ username, onChange, onSubmit, onCancel, message }) => (
    <div className="auth-form">
      <h2>Qun mt khu</h2>
      <p>Vui lng nhp tn ti khon ca bn</p>
      <input
        type="text"
        name="username"
        placeholder="Tn ti khon"
        className="account-input"
        value={username}
        onChange={onChange}
        aria-label="Nhp tn ti khon"
      />
      {message && (
        <p className={`message ${message.includes("thnh cng") ? "success" : "error"}`}>
          {message}
        </p>
      )}
      <div className="account-buttons">
        <button
          className="account-button verify-btn"
          onClick={onSubmit}
          aria-label="Gi m xc nhn"
        >
          Gi m xc nhn
        </button>
        <button
          className="link-button"
          onClick={onCancel}
          aria-label="Hy qun mt khu"
        >
          Hy
        </button>
      </div>
    </div>
  )
);

/**
 * Form nhp m xc nhn v i mt khu
 * @param {Object} props - Props cha thng tin m, mt khu v cc hm x l
 * @returns {JSX.Element} JSX hin th form qun mt khu
 */
const ForgotPasswordResetForm = React.memo(
  ({ email, verificationCode, newPassword, confirmPassword, onCodeChange, onPasswordChange, onConfirmChange, onSubmit, onCancel, message }) => (
    <div className="auth-form">
      <h2>Qun mt khu</h2>
      <p>M xc nhn  c gi n {email}</p>
      <input
        type="text"
        placeholder="Nhp m xc nhn"
        className="account-input"
        value={verificationCode}
        onChange={onCodeChange}
        aria-label="Nhp m xc nhn"
      />
      <input
        type="password"
        placeholder="Mt khu mi"
        className="account-input"
        value={newPassword}
        onChange={onPasswordChange}
        aria-label="Nhp mt khu mi"
      />
      <input
        type="password"
        placeholder="Xc nhn mt khu mi"
        className="account-input"
        value={confirmPassword}
        onChange={onConfirmChange}
        aria-label="Xc nhn mt khu mi"
      />
      {message && (
        <p className={`message ${message.includes("thnh cng") ? "success" : "error"}`}>
          {message}
        </p>
      )}
      <div className="account-buttons">
        <button
          className="account-button verify-btn"
          onClick={onSubmit}
          aria-label="Xc nhn v i mt khu"
        >
          Xc nhn
        </button>
        <button
          className="link-button"
          onClick={onCancel}
          aria-label="Hy qun mt khu"
        >
          Hy
        </button>
      </div>
    </div>
  )
);

/**
 * Form nhp m xc nhn (ng k)
 * @param {Object} props - Props cha thng tin m v cc hm x l
 * @returns {JSX.Element} JSX hin th form xc nhn
 */
const VerificationForm = React.memo(
  ({ email, verificationCode, onCodeChange, onVerify, onCancel, message }) => (
    <div className="auth-form">
      <h2>Xc nhn m</h2>
      <p>Vui lng nhp m xc nhn  c gi n {email}</p>
      <input
        type="text"
        placeholder="Nhp m xc nhn"
        className="account-input"
        value={verificationCode}
        onChange={onCodeChange}
        aria-label="Nhp m xc nhn"
      />
      {message && (
        <p className={`message ${message.includes("thnh cng") ? "success" : "error"}`}>
          {message}
        </p>
      )}
      <div className="account-buttons">
        <button
          className="account-button verify-btn"
          onClick={onVerify}
          aria-label="Xc nhn m"
        >
          Xc nhn
        </button>
        <button
          className="link-button"
          onClick={onCancel}
          aria-label="Hy xc nhn"
        >
          Hy
        </button>
      </div>
    </div>
  )
);

/**
 * Giao din khi  ng nhp
 * @param {Object} props - Props cha hm ng xut v thng bo
 * @returns {JSX.Element} JSX hin th section  ng nhp
 */
const LoggedInSection = React.memo(({ onLogout, message }) => (
  <div className="logged-in-section">
    <p>Bn  ng nhp thnh cng!</p>
    <button
      className="account-button logout-btn"
      onClick={onLogout}
      aria-label="ng xut"
    >
      ng xut
    </button>
    {message && (
      <p className={`message ${message.includes("thnh cng") ? "success" : "error"}`}>
        {message}
      </p>
    )}
  </div>
));

// --- THNH PHN CHNH ---

/**
 * Trang qun l ti khon (ng nhp, ng k, qun mt khu)
 * @returns {JSX.Element} JSX hin th trang ti khon
 */
const Account = () => {
  const navigate = useNavigate();
  const { user, isLoggedIn, login, logout } = useContext(AuthContext) || {
    user: null,
    isLoggedIn: false,
    login: () => {},
    logout: () => {},
  };

  const [isRegistering, setIsRegistering] = useState(false);
  const [isVerifying, setIsVerifying] = useState(false);
  const [isForgotPassword, setIsForgotPassword] = useState(false);
  const [isForgotUsernameStep, setIsForgotUsernameStep] = useState(true);
  const [formData, setFormData] = useState({ username: "", password: "", email: "" });
  const [forgotUsername, setForgotUsername] = useState("");
  const [verificationCode, setVerificationCode] = useState("");
  const [generatedCode, setGeneratedCode] = useState("");
  const [pendingUser, setPendingUser] = useState(null);
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [message, setMessage] = useState("");

  // iu hng v trang ch nu  ng nhp
  useEffect(() => {
    if (isLoggedIn) {
      navigate("/home");
    }
  }, [isLoggedIn, navigate]);

  // Khi to EmailJS
  useEffect(() => {
    emailjs.init(process.env.REACT_APP_EMAILJS_PUBLIC_KEY);
    console.log("EmailJS khi to trong Account.js");
  }, []);

  // X l thay i gi tr trong form ng nhp/ng k
  const handleChange = useCallback((e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
    setMessage("");
  }, []);

  // X l thay i username khi qun mt khu
  const handleForgotUsernameChange = useCallback((e) => {
    setForgotUsername(e.target.value);
    setMessage("");
  }, []);

  // X l thay i m xc nhn
  const handleCodeChange = useCallback((e) => {
    setVerificationCode(e.target.value);
    setMessage("");
  }, []);

  // X l thay i mt khu mi
  const handlePasswordChange = useCallback((e) => {
    setNewPassword(e.target.value);
    setMessage("");
  }, []);

  // X l thay i xc nhn mt khu
  const handleConfirmChange = useCallback((e) => {
    setConfirmPassword(e.target.value);
    setMessage("");
  }, []);

  // X l ng k (gi m xc nhn)
  const handleRegister = useCallback(() => {
    const { username, password, email } = formData;

    if (!username.trim() || !password.trim() || !email.trim()) {
      setMessage(MESSAGES.EMPTY_FIELDS);
      return;
    }

    const storedUsers = getStoredUsers();
    if (storedUsers.some((u) => u.username === username)) {
      setMessage(MESSAGES.USER_EXISTS);
      return;
    }

    const code = generateVerificationCode();
    setGeneratedCode(code);
    setPendingUser({ username, password, email });
    sendVerificationEmail({ username, email }, code, setMessage);
    setIsVerifying(true);
  }, [formData]);

  // X l xc nhn m (ng k)
  const handleVerifyCode = useCallback(() => {
    if (!verificationCode.trim()) {
      setMessage(MESSAGES.EMPTY_CODE);
      return;
    }

    if (verificationCode === generatedCode) {
      const newUser = { ...pendingUser, email: pendingUser.email || "" };
      const storedUsers = getStoredUsers();
      saveUsersToStorage([...storedUsers, newUser]);
      setMessage(MESSAGES.REGISTER_SUCCESS);
      setFormData({ username: "", password: "", email: "" });
      setVerificationCode("");
      setGeneratedCode("");
      setPendingUser(null);
      setIsVerifying(false);
      setTimeout(() => setIsRegistering(false), 1000);
    } else {
      setMessage(MESSAGES.INVALID_CODE);
    }
  }, [verificationCode, generatedCode, pendingUser]);

  // X l nhp username v gi m xc nhn khi qun mt khu
  const handleForgotPasswordUsername = useCallback(() => {
    if (!forgotUsername.trim()) {
      setMessage(MESSAGES.INVALID_USERNAME);
      return;
    }

    const storedUsers = getStoredUsers();
    const foundUser = storedUsers.find((u) => u.username === forgotUsername && u.email);
    if (!foundUser || !foundUser.email) {
      setMessage(MESSAGES.USER_NOT_FOUND);
      return;
    }

    const code = generateVerificationCode();
    setGeneratedCode(code);
    setPendingUser(foundUser);
    sendVerificationEmail({ username: foundUser.username, email: foundUser.email }, code, setMessage);
    setIsForgotUsernameStep(false);
  }, [forgotUsername]);

  // X l xc nhn m v i mt khu
  const handleResetPassword = useCallback(() => {
    if (!verificationCode.trim()) {
      setMessage(MESSAGES.EMPTY_CODE);
      return;
    }

    if (verificationCode !== generatedCode) {
      setMessage(MESSAGES.INVALID_CODE);
      return;
    }

    if (!newPassword.trim() || !confirmPassword.trim()) {
      setMessage(MESSAGES.EMPTY_FIELDS);
      return;
    }

    if (newPassword !== confirmPassword) {
      setMessage(MESSAGES.PASSWORD_MISMATCH);
      return;
    }

    // Cp nht mt khu trong danh sch ngi dng
    const storedUsers = getStoredUsers();
    const userIndex = storedUsers.findIndex((u) => u.username === pendingUser.username && u.email === pendingUser.email);
    if (userIndex === -1) {
      setMessage("Li: Khng tm thy ngi dng  cp nht mt khu.");
      return;
    }

    // Cp nht mt khu mi
    storedUsers[userIndex].password = newPassword;
    saveUsersToStorage(storedUsers);

    // Reset trng thi sau khi i mt khu thnh cng
    setMessage(MESSAGES.RESET_SUCCESS);
    setForgotUsername("");
    setVerificationCode("");
    setNewPassword("");
    setConfirmPassword("");
    setGeneratedCode("");
    setPendingUser(null);
    setIsForgotPassword(false);
    setIsForgotUsernameStep(true);
    setTimeout(() => navigate("/"), 1000);
  }, [verificationCode, generatedCode, newPassword, confirmPassword, pendingUser, navigate]);

  // X l ng nhp
  const handleLogin = useCallback(() => {
    const { username, password } = formData;

    if (!username.trim() || !password.trim()) {
      setMessage(MESSAGES.EMPTY_FIELDS);
      return;
    }

    if (username === ADMIN_CREDENTIALS.USERNAME && password === ADMIN_CREDENTIALS.PASSWORD) {
      login({ username: ADMIN_CREDENTIALS.USERNAME, email: "", role: "admin" });
      setMessage(MESSAGES.LOGIN_SUCCESS);
      navigate("/admin");
      return;
    }

    const storedUsers = getStoredUsers();
    const foundUser = storedUsers.find(
      (u) => u.username === username && u.password === password
    );

    if (foundUser) {
      login(foundUser);
      setMessage(MESSAGES.LOGIN_SUCCESS);
    } else {
      setMessage(MESSAGES.LOGIN_FAILED);
    }
  }, [formData, login, navigate]);

  // X l ng xut
  const handleLogout = useCallback(() => {
    logout();
    setMessage(MESSAGES.LOGOUT_SUCCESS);
    setFormData({ username: "", password: "", email: "" });
    setTimeout(() => navigate("/"), 1000);
  }, [logout, navigate]);

  // X l chuyn i gia cc ch 
  const handleToggle = useCallback((register = false, forgot = false) => {
    setIsRegistering(register);
    setIsForgotPassword(forgot);
    setIsForgotUsernameStep(true);
    setMessage("");
    setFormData({ username: "", password: "", email: "" });
    setForgotUsername("");
    setVerificationCode("");
    setNewPassword("");
    setConfirmPassword("");
  }, []);

  // Hy xc nhn m hoc qun mt khu
  const handleCancel = useCallback(() => {
    setIsVerifying(false);
    setIsForgotPassword(false);
    setIsForgotUsernameStep(true);
    setMessage("");
    setVerificationCode("");
    setNewPassword("");
    setConfirmPassword("");
    setForgotUsername("");
  }, []);

  // Tiu  trang
  const pageTitle = isLoggedIn
    ? `Xin cho, ${user?.username || "Ngi dng"}!`
    : isRegistering
    ? isVerifying
      ? "Xc nhn m"
      : "ng k ti khon"
    : isForgotPassword
    ? isForgotUsernameStep
      ? "Qun mt khu"
      : "i mt khu"
    : "ng nhp";

  return (
    <div className="account-container">
      <div className="account-box">
        <h1>{pageTitle}</h1>
        {isLoggedIn ? (
          <LoggedInSection onLogout={handleLogout} message={message} />
        ) : isVerifying ? (
          <VerificationForm
            email={formData.email}
            verificationCode={verificationCode}
            onCodeChange={handleCodeChange}
            onVerify={handleVerifyCode}
            onCancel={handleCancel}
            message={message}
          />
        ) : isForgotPassword ? (
          isForgotUsernameStep ? (
            <ForgotPasswordUsernameForm
              username={forgotUsername}
              onChange={handleForgotUsernameChange}
              onSubmit={handleForgotPasswordUsername}
              onCancel={handleCancel}
              message={message}
            />
          ) : (
            <ForgotPasswordResetForm
              email={pendingUser?.email}
              verificationCode={verificationCode}
              newPassword={newPassword}
              confirmPassword={confirmPassword}
              onCodeChange={handleCodeChange}
              onPasswordChange={handlePasswordChange}
              onConfirmChange={handleConfirmChange}
              onSubmit={handleResetPassword}
              onCancel={handleCancel}
              message={message}
            />
          )
        ) : (
          <AuthForm
            formData={formData}
            isRegistering={isRegistering}
            onChange={handleChange}
            onSubmit={isRegistering ? handleRegister : handleLogin}
            onToggle={handleToggle}
            message={message}
          />
        )}
      </div>
    </div>
  );
};

export default Account;