import React, { useContext, useCallback, useMemo } from "react";
import { Link, useNavigate } from "react-router-dom";
import { CartContext } from "../pages/CartContext";
import { AuthContext } from "../account/AuthContext";
import "./Header.css";

/**
 * Hm tin ch  tnh tng s lng sn phm trong gi hng
 * @param {Array} cart - Mng cha cc sn phm trong gi hng
 * @returns {number} - Tng s lng sn phm
 */
const calculateCartItemCount = (cart) => {
  const safeCart = Array.isArray(cart) ? cart : [];
  return safeCart.reduce((sum, item) => sum + (item?.quantity || 0), 0);
};

/**
 * Hm tin ch  ly tn hin th ca ngi dng
 * @param {Object} user - i tng cha thng tin ngi dng
 * @returns {string} - Tn hin th ca ngi dng
 */
const getDisplayName = (user) => user?.username || "Khch";

/**
 * Component Header - Hin th thanh iu hng chnh ca ng dng
 * Bao gm logo, cc nt ng nhp/ng xut, gi hng v cc lin kt khc
 */
const Header = () => {
  // Ly thng tin gi hng t CartContext
  const { cart } = useContext(CartContext);
  
  // Ly thng tin ngi dng v cc hm xc thc t AuthContext
  const { user, isLoggedIn, logout } = useContext(AuthContext) || {
    user: null,
    isLoggedIn: false,
    logout: () => {},
  };
  
  // Hook  iu hng trang
  const navigate = useNavigate();

  // Ti u ha vic tnh ton s lng sn phm trong gi hng
  const cartItemCount = useMemo(() => calculateCartItemCount(cart), [cart]);

  /**
   * X l s kin ng xut
   * ng xut ngi dng v chuyn hng v trang ch
   */
  const handleLogout = useCallback(() => {
    logout();
    navigate("/");
  }, [logout, navigate]);

  /**
   * Render cc nt iu hng cho ngi dng  ng nhp
   * Bao gm: thng tin ngi dng, nt ng xut, gi hng v lin kt admin (nu c quyn)
   */
  const renderAuthenticatedActions = () => (
    <>
      <Link to="/profile" className="profile-button" aria-label="Xem thng tin ngi dng">
         Xin Cho {getDisplayName(user)}
      </Link>
      <button
        className="logout-button"
        onClick={handleLogout}
        aria-label="ng xut"
      >
         ng xut
      </button>
      <Link to="/cart" className="cart-button" aria-label="Xem gi hng">
         Gi hng ({cartItemCount})
      </Link>
      {user?.role === 'admin' && (
        <Link to="/admin" className="admin-link" aria-label="Truy cp trang qun tr">
           Admin
        </Link>
      )}
    </>
  );

  /**
   * Render nt ng nhp cho ngi dng cha ng nhp
   */
  const renderUnauthenticatedActions = () => (
    <Link to="/" className="login-button" aria-label="ng nhp hoc ng k">
       ng nhp
    </Link>
  );

  console.log('User addresses:', user?.addresses);

  return (
    <header className="header">
      {/* Logo v tn ca hng */}
      <Link to="/home" className="store-title">
         MobileStore
      </Link>
      
      {/* Khu vc cha cc nt iu hng */}
      <div className="header-actions">
        {isLoggedIn ? renderAuthenticatedActions() : renderUnauthenticatedActions()}
      </div>
    </header>
  );
};

export default Header;