import React, { useContext } from "react"; // Import hook useContext t th vin React
// Import cc thnh phn cn thit t react-router-dom cho vic nh tuyn:
// HashRouter s dng du '#' trong URL (thng dng cho cc ng dng n gin hoc deploy trn server tnh)
// Routes bao bc cc nh ngha Route
// Route nh ngha mt route c th (path v component tng ng)
// Navigate dng  iu hng chng trnh (thay th Redirect trong v5)
import { HashRouter as Router, Routes, Route, Navigate } from "react-router-dom";

// Import cc page v component t th mc tng ng:
import ProductPage from "./pages/ProductPage"; // Trang danh sch sn phm
// Import ProductDetail t th mc pages (ng nh comment  ghi nhn chuyn sang pages)
import ProductDetail from "./components/ProductDetail"; // Trang chi tit sn phm
import Account from "./account/Account"; // Trang ng nhp/ng k/ng xut
import CartPage from "./pages/CartPage"; // Trang gi hng
import OrderHistory from "./pages/OrderHistory"; // Trang lch s n hng
import AdminDashboard from "./pages/AdminDashboard"; // Component AdminDashboard
import UserProfilePage from "./pages/UserProfilePage"; // Component UserProfilePage (trang h s ngi dng)
import CheckoutPage from "./pages/CheckoutPage";
// Import AuthProvider (default export) v AuthContext (named export) t cng file
import AuthProvider, { AuthContext } from "./account/AuthContext";
// Import CartProvider (named export) t file CartContext
import { CartProvider } from "./pages/CartContext";

// Import cc component layout chung
import Footer from "./components/Footer"; // Component Footer chung
import Header from "./components/Header"; // Component Header chung

// --- Component ProtectedRoute - Bo v cc route yu cu ng nhp ---
// Component ny nhn cc component con (children) lm prop.
// N kim tra trng thi ng nhp v ch render children nu  ng nhp,
// ngc li s iu hng v trang ng nhp.
const ProtectedRoute = ({ children }) => {
  // S dng useContext  ly AuthContext.
  // AuthContext c cung cp bi AuthProvider bc pha trn.
  const authContext = useContext(AuthContext);
  // Gii cu trc isLoggedIn t context.
  // Cung cp gi tr mc nh { isLoggedIn: false }  trnh li nu context cha sn sng
  // hoc AuthContext cha c cung cp ng cch.
  const { isLoggedIn } = authContext || { isLoggedIn: false };

  // Nu trng thi isLoggedIn l false (cha ng nhp),
  // s dng component Navigate  chuyn hng ngi dng.
  if (!isLoggedIn) {
    // Navigate to "/" (trang ng nhp/ng k)
    // replace prop: Khi iu hng, n s thay th entry hin ti trong lch s trnh duyt
    // thay v thm mt entry mi. iu ny gip ngi dng khng th quay li trang b bo v
    // bng cch nhn nt back ca trnh duyt.
    return <Navigate to="/" replace />;
  }

  // Nu trng thi isLoggedIn l true ( ng nhp),
  // component s render cc children (trang c bo v) m n bao bc.
  return children;
};

// --- Component App - nh ngha cu trc ng dng v routing ---
// y l component gc ca ng dng React.
// N thit lp Context Providers v cu hnh React Router  iu hng gia cc trang.
const App = () => {
  return (
    // Bc ton b ng dng bng AuthProvider  cung cp Context xc thc
    // cho tt c cc component con c th truy cp.
    <AuthProvider>
      {/* Bc phn ng dng cn truy cp Context gi hng bng CartProvider */}
      {/* Thng thng AuthProvider s bc CartProvider v CartContext c th cn thng tin user */}
      <CartProvider>
        {/* Bc ton b phn nh tuyn ca ng dng bng Router */}
        <Router>
          {/* Header v Footer l cc component layout chung,
              chng c t bn ngoi Routes  hin th  tt c cc trang */}
          <Header />

          {/* Mt div container c th thm CSS  to padding cho ni dung chnh,
              trnh b header/footer che khut. */}
          <div className="app-content">
            {/* Routes component cha tt c cc nh ngha Route ca ng dng */}
            <Routes>
              {/* Route cho trang ng nhp/ng k. y l trang cng khai (khng cn ng nhp). */}
              <Route path="/" element={<Account />} />
              {/* C th thm cc route cng khai khc ti y nu cn */}

              {/* --- Cc Route c bo v (yu cu ngi dng phi ng nhp) --- */}
              {/* S dng component ProtectedRoute  bao bc cc component trang cn bo v.
                  Nu ngi dng cha ng nhp khi truy cp cc path ny, ProtectedRoute s chuyn hng h v trang "/" (Account). */}
              <Route
                path="/home"
                element={
                  <ProtectedRoute> {/* Bc ProductPage bng ProtectedRoute */}
                    <ProductPage /> {/* Trang danh sch sn phm */}
                  </ProtectedRoute>
                }
              />
              <Route
                path="/products/:id"
                element={
                  <ProtectedRoute> {/* Bc ProductDetail bng ProtectedRoute */}
                    {/* ProductDetail nhn ID sn phm t URL thng qua useParams hook */}
                    <ProductDetail /> {/* Trang chi tit sn phm */}
                  </ProtectedRoute>
                }
              />
              <Route
                path="/cart"
                element={
                  <ProtectedRoute> {/* Bc CartPage bng ProtectedRoute */}
                    <CartPage /> {/* Trang gi hng */}
                  </ProtectedRoute>
                }
              />
              <Route
                path="/orders"
                element={
                  <ProtectedRoute> {/* Bc OrderHistory bng ProtectedRoute */}
                    <OrderHistory /> {/* Trang lch s n hng */}
                  </ProtectedRoute>
                }
              />
              <Route
                path="/checkout"
                element={
                  <ProtectedRoute>
                    <CheckoutPage /> {/* Trang Thanh ton */}
                  </ProtectedRoute>
                }
              />
               <Route
                path="/profile"
                element={
                  <ProtectedRoute> {/* Bc UserProfilePage bng ProtectedRoute */}
                    <UserProfilePage /> {/* Trang h s ngi dng */}
                  </ProtectedRoute>
                }
              />


              {/* --- Route cho trang Admin Dashboard --- */}
              {/* Route ny hin ti CHA c bo v bi ProtectedRoute.
                   bo v route admin, bn s cn to mt AdminProtectedRoute
                  kim tra c trng thi ng nhp V vai tr (role) ca ngi dng. */}
              <Route path="/admin" element={<AdminDashboard />} />
               {/* C th thm cc Route khc cho admin dashboard ti y nu cn */}


              {/* --- Route x l cc URL khng khp (Fallback/404) --- */}
              {/* Route vi path="*" s khp vi bt k URL no khng khp vi cc Route  nh ngha  trn. */}
              {/* S dng Navigate  chuyn hng ngi dng v trang gc "/" (trang ng nhp/ng k). */}
              {/* Bn c th thay i ch n ny ty theo logic ng dng (v d: v trang ch nu  ng nhp). */}
              <Route path="*" element={<Navigate to="/" replace />} />

            </Routes>
          </div>

          {/* Footer l component layout chung, hin th  tt c cc trang */}
          <Footer />
        </Router>
      </CartProvider>
    </AuthProvider>
  );
};

export default App; // Export component App lm default export  s dng trong file entry point (thng l index.js)