// --- Import cc module cn thit ---
// React v cc hook c bn: useReducer, useContext, useEffect, useCallback
import React, { useReducer, useContext, useEffect, useCallback } from "react";
// Hook t react-router-dom  iu hng v to link
import { useNavigate, Link } from "react-router-dom";
// Context  qun l trng thi xc thc ngi dng
import { AuthContext } from "../account/AuthContext"; // Gi nh ng dn ny l chnh xc
// Component hin th lch s n hng
import OrderHistory from "./OrderHistory"; // Gi nh ng dn ny l chnh xc
// Th vin  kim tra kiu d liu ca props
import PropTypes from 'prop-types';
// Import file CSS cho UserProfilePage
import "./UserProfilePage.css"; // Gi nh ng dn ny l chnh xc

// --- nh ngha cc hng s ---
// Kha s dng cho vic lu tr danh sch ngi dng trong localStorage
const LOCAL_STORAGE_USERS_KEY = "users";
//  di ti thiu cho mt khu mi
const MIN_PASSWORD_LENGTH = 6;

// i tng cha cc thng bo s hin th cho ngi dng
const MESSAGES = {
  PASSWORD_CHANGE_SUCCESS: "i mt khu thnh cng! Vui lng ng nhp li.",
  PASSWORD_CHANGE_FAILED: "i mt khu tht bi. Vui lng kim tra mt khu c.",
  PASSWORDS_NOT_MATCH: "Mt khu mi v xc nhn mt khu khng khp.",
  EMPTY_PASSWORD_FIELDS: "Vui lng in y  cc trng mt khu.",
  PASSWORD_SAME_AS_OLD: "Mt khu mi khng c trng vi mt khu c!",
  PASSWORD_TOO_SHORT: `Mt khu mi phi c t nht ${MIN_PASSWORD_LENGTH} k t!`,
  PROFILE_UPDATE_SUCCESS: "Cp nht thng tin thnh cng!",
  PROFILE_UPDATE_FAILED: "Cp nht thng tin tht bi. Vui lng th li.",
  ADDRESS_SAVE_SUCCESS: "Lu a ch thnh cng!",
  ADDRESS_SAVE_FAILED: "Lu a ch tht bi. Vui lng th li.",
  ADDRESS_DELETE_SUCCESS: "Xa a ch thnh cng!",
  ADDRESS_EMPTY_FIELDS: "Vui lng in  thng tin a ch, tn v s in thoi.",
  SYSTEM_ERROR_READING_USERS: "Li h thng, khng th c d liu ngi dng.",
  SYSTEM_ERROR_UPDATING_USERS: "Li h thng, khng th lu d liu ngi dng.",
  USER_NOT_FOUND: "Khng tm thy thng tin ngi dng hin ti.",
  LOGIN_REQUIRED: "Vui lng ng nhp  xem trang h s.",
  INVALID_PROPS: "D liu u vo khng hp l.",
  INVALID_OLD_PASSWORD: "Mt khu c khng chnh xc.", // Thm thng bo ny
  PASSWORD_MISMATCH: "Mt khu mi v xc nhn mt khu khng khp.", // Thm thng bo ny
  ADDRESS_REQUIRED: "Vui lng nhp a ch.", // Thm thng bo ny
  NAME_REQUIRED: "Vui lng nhp tn ngi nhn.", // Thm thng bo ny
  INVALID_PHONE: "S in thoi khng hp l. Phi c 10-11 ch s.", // Thm thng bo ny
};

// Cc loi form khc nhau trong trang h s
const FORM_TYPES = {
  PROFILE: "profile",     // Form thng tin c nhn
  PASSWORD: "password",   // Form i mt khu
  ADDRESS: "address",     // Form qun l a ch
};

// Cc loi hnh ng (action) s dng trong reducer
const ACTION_TYPES = {
  SET_ACTIVE_SECTION: "SET_ACTIVE_SECTION", // t mc ang hot ng (profile, password, ...)
  UPDATE_FORM_STATE: "UPDATE_FORM_STATE",   // Cp nht trng thi ca mt form c th
  SET_MESSAGE: "SET_MESSAGE",               // t thng bo cho mt form c th
  RESET_FORM: "RESET_FORM",                 // Reset trng thi ca mt form v gi tr mc nh
};

// Danh sch cc mc (section) trong trang h s ngi dng
const SECTIONS = [
  { id: FORM_TYPES.PROFILE, label: "Thng tin c nhn" },
  { id: FORM_TYPES.PASSWORD, label: "i mt khu" },
  { id: "addresses", label: "a ch giao hng" }, // S dng FORM_TYPES.ADDRESS s tt hn
  { id: "orders", label: "Lch s n hng" },
];

// Thi gian ch (miligiy) trc khi t ng ng xut sau khi i mt khu thnh cng
const LOGOUT_DELAY = 2000;

// --- Trng thi khi to cho reducer ---
// nh ngha cu trc trng thi ban u cho ton b trang h s
const initialState = {
  activeSection: FORM_TYPES.PROFILE, // Mc ang c hin th mc nh l "Thng tin c nhn"
  profile: { username: "", email: "", phone: "", message: "" }, // Trng thi cho form thng tin c nhn
  password: { oldPassword: "", newPassword: "", confirmNewPassword: "", message: "" }, // Trng thi cho form i mt khu
  address: { addresses: [], newAddress: { address: "", name: "", phone: "" }, message: "" }, // Trng thi cho form a ch
};

// --- Reducer function  qun l trng thi ---
// Hm reducer nhn vo trng thi hin ti v mt hnh ng, tr v trng thi mi
const profileReducer = (state, action) => {
  switch (action.type) {
    // Trng hp t mc ang hot ng
    case ACTION_TYPES.SET_ACTIVE_SECTION:
      return { ...state, activeSection: action.payload }; // Cp nht activeSection vi gi tr t payload

    // Trng hp cp nht trng thi ca mt form
    case ACTION_TYPES.UPDATE_FORM_STATE: {
      const { formType, data } = action.payload; // Ly loi form v d liu mi t payload
      return {
        ...state, // Gi nguyn cc phn khc ca state
        [formType]: { ...state[formType], ...data, message: "" }, // Cp nht form c th, xa thng bo c
      };
    }
    // Trng hp t thng bo cho mt form
    case ACTION_TYPES.SET_MESSAGE: {
      const { formType, message } = action.payload; // Ly loi form v thng bo t payload
      return {
        ...state, // Gi nguyn cc phn khc ca state
        [formType]: { ...state[formType], message }, // Cp nht thng bo cho form c th
      };
    }
    // Trng hp reset mt form
    case ACTION_TYPES.RESET_FORM: {
      const { formType, defaultData, message } = action.payload; // Ly loi form, d liu mc nh v thng bo
      return {
        ...state, // Gi nguyn cc phn khc ca state
        [formType]: { ...defaultData, message }, // Reset form v d liu mc nh v t thng bo mi
      };
    }
    // Trng hp mc nh nu khng c action no khp
    default:
      return state; // Tr v trng thi hin ti
  }
};

// --- Cc hm tin ch ---

/**
 * c danh sch ngi dng t localStorage.
 * @returns {Array|null} Mng cc i tng ngi dng, hoc null nu c li.
 */
const readUsersFromStorage = () => {
  try {
    // Ly d liu t localStorage vi kha LOCAL_STORAGE_USERS_KEY
    const usersData = localStorage.getItem(LOCAL_STORAGE_USERS_KEY);
    // Parse chui JSON thnh mng, nu khng c d liu th tr v mng rng
    return JSON.parse(usersData || "[]");
  } catch (error) {
    // Ghi li ra console nu c vn  khi parse JSON
    console.error(MESSAGES.SYSTEM_ERROR_READING_USERS, error);
    return null; // Tr v null nu c li
  }
};

/**
 * Lu danh sch ngi dng vo localStorage.
 * @param {Array} users Mng cc i tng ngi dng cn lu.
 * @returns {boolean} True nu lu thnh cng, false nu c li.
 */
const saveUsersToStorage = (users) => {
  try {
    // Chuyn i mng ngi dng thnh chui JSON v lu vo localStorage
    localStorage.setItem(LOCAL_STORAGE_USERS_KEY, JSON.stringify(users));
    return true; // Tr v true nu lu thnh cng
  } catch (error) {
    // Ghi li ra console nu c vn  khi lu
    console.error(MESSAGES.SYSTEM_ERROR_UPDATING_USERS, error);
    return false; // Tr v false nu c li
  }
};

/**
 * Kim tra tnh hp l ca thng tin i mt khu.
 * @param {object} passwords i tng cha oldPassword, newPassword, confirmNewPassword.
 * @param {string} currentPassword Mt khu hin ti ca ngi dng.
 * @returns {string|null} Thng bo li nu khng hp l, null nu hp l.
 */
const validatePasswordChange = ({ oldPassword, newPassword, confirmNewPassword }, currentPassword) => {
  // Kim tra mt khu c c khp khng
  if (oldPassword !== currentPassword) return MESSAGES.INVALID_OLD_PASSWORD;
  // Kim tra  di mt khu mi
  if (newPassword.length < MIN_PASSWORD_LENGTH) return MESSAGES.PASSWORD_TOO_SHORT;
  // Kim tra mt khu mi v xc nhn mt khu c khp khng
  if (newPassword !== confirmNewPassword) return MESSAGES.PASSWORDS_NOT_MATCH;
  // Kim tra mt khu mi c trng mt khu c khng
  if (newPassword === oldPassword) return MESSAGES.PASSWORD_SAME_AS_OLD;
  // Nu tt c u hp l
  return null;
};

/**
 * Kim tra tnh hp l ca thng tin a ch.
 * @param {object} addressData i tng cha address, name, phone.
 * @returns {string|null} Thng bo li nu khng hp l, null nu hp l.
 */
const validateAddress = ({ address, name, phone }) => {
  // Kim tra a ch c c nhp khng (sau khi  loi b khong trng tha)
  if (!address.trim()) return MESSAGES.ADDRESS_REQUIRED;
  // Kim tra tn ngi nhn c c nhp khng
  if (!name.trim()) return MESSAGES.NAME_REQUIRED;
  // Kim tra s in thoi c c nhp khng v c ng nh dng (10-11 ch s)
  if (!phone.trim() || !/^\d{10,11}$/.test(phone.trim())) return MESSAGES.INVALID_PHONE;
  // Nu tt c u hp l
  return null;
};

