// --- Component chnh: UserProfilePage ---
const UserProfilePage = () => {
  // Ly thng tin xc thc t AuthContext
  const authContext = useContext(AuthContext);

  // Kim tra nu AuthContext khng c cung cp (v d: UserProfilePage khng nm trong AuthProvider)
  if (!authContext) {
    // Ghi li ra console. Trong ng dng thc t, nn c ErrorBoundary hoc x l tt hn.
    console.error("AuthContext must be provided. Ensure UserProfilePage is wrapped in AuthProvider.");
    // C th hin th mt thng bo li cho ngi dng hoc tr v null  khng render g c.
    return <p>Li: AuthContext khng kh dng. Vui lng lin h qun tr vin.</p>;
  }
  // Ly cc gi tr user, isLoggedIn, login, logout t context
  const { user, isLoggedIn, login, logout } = authContext;

  // Hook  iu hng gia cc trang
  const navigate = useNavigate();
  // S dng useReducer  qun l trng thi ca trang h s, vi profileReducer v initialState  nh ngha
  const [state, dispatch] = useReducer(profileReducer, initialState);

  /**
   * Hm memoized  cp nht d liu ngi dng trong localStorage v AuthContext.
   * @param {object} updatedUserData D liu ngi dng cn cp nht.
   * @returns {object} i tng cha { success: boolean, message: string }.
   */
  const updateUserAndContext = useCallback(
    (updatedUserData) => {
      // c danh sch ngi dng hin ti t localStorage
      const storedUsers = readUsersFromStorage();
      // Nu khng c c hoc c li
      if (!storedUsers) {
        return { success: false, message: MESSAGES.SYSTEM_ERROR_READING_USERS };
      }
      // Nu khng c thng tin ngi dng hin ti trong context (cha ng nhp hoc li)
      if (!user?.username) {
        return { success: false, message: MESSAGES.USER_NOT_FOUND };
      }

      // Tm v tr (index) ca ngi dng hin ti trong danh sch  c t localStorage
      const userIndex = storedUsers.findIndex((u) => u.username === user.username);
      // Nu khng tm thy ngi dng trong localStorage
      if (userIndex === -1) {
        return { success: false, message: MESSAGES.USER_NOT_FOUND };
      }

      // To i tng ngi dng mi bng cch kt hp d liu c v d liu cp nht
      const updatedUser = { ...storedUsers[userIndex], ...updatedUserData };
      // Cp nht thng tin ngi dng ti v tr  tm thy trong mng
      storedUsers[userIndex] = updatedUser;

      // Lu li ton b danh sch ngi dng  cp nht vo localStorage
      const saveSuccess = saveUsersToStorage(storedUsers);
      // Nu lu thnh cng
      if (saveSuccess) {
        // Cp nht thng tin ngi dng trong AuthContext ( cc component khc cng c d liu mi)
        login(updatedUser); // Hm login t AuthContext c th c dng  cp nht user
        return { success: true, message: "" }; // Tr v thnh cng
      }
      // Nu lu tht bi
      return { success: false, message: MESSAGES.SYSTEM_ERROR_UPDATING_USERS };
    },
    [user, login] // Dependencies: hm s c to li nu user hoc login thay i
  );

  // useEffect  khi to d liu ngi dng khi component c mount hoc khi user thay i
  useEffect(() => {
    // Nu ngi dng cha ng nhp hoc khng c thng tin user.username
    if (!isLoggedIn || !user?.username) {
      // Hin th thng bo yu cu ng nhp.
      // Lu : alert() c th b chn trong mt s mi trng (v d: iframe) v khng phi l cch tt nht  hin th thng bo.
      // Cn nhc s dng mt modal hoc component thng bo ty chnh.
      alert(MESSAGES.LOGIN_REQUIRED);
      // iu hng ngi dng v trang ch (hoc trang ng nhp)
      navigate("/"); // iu hng v trang ch
      return; // Kt thc sm  khng chy code pha di
    }

    // Nu  ng nhp, dispatch action  cp nht form thng tin c nhn vi d liu t user context
    dispatch({
      type: ACTION_TYPES.UPDATE_FORM_STATE,
      payload: {
        formType: FORM_TYPES.PROFILE,
        data: {
          username: user.username,
          email: user.email || "", // S dng "" nu email l null/undefined
          phone: user.phone || "", // S dng "" nu phone l null/undefined
        },
      },
    });

    // Dispatch action  cp nht form a ch vi danh sch a ch t user context
    dispatch({
      type: ACTION_TYPES.UPDATE_FORM_STATE,
      payload: {
        formType: FORM_TYPES.ADDRESS,
        data: { addresses: user.addresses || [] }, // S dng mng rng nu addresses l null/undefined
      },
    });
  }, [isLoggedIn, navigate, user]); // Dependencies: effect chy li khi isLoggedIn, navigate, hoc user thay i

  /**
   * Hm memoized x l thay i gi tr trong cc input ca form.
   * @param {Event} e S kin thay i input.
   ** @param {string} formType Loi form (profile, password, address).
   */
  const handleInputChange = useCallback(
    (e, formType) => {
      const { name, value } = e.target; // Ly tn v gi tr ca input ang thay i
      let data; // Bin  cha d liu cp nht cho state

      // Nu l form a ch, cu trc d liu cp nht s khc
      if (formType === FORM_TYPES.ADDRESS) {
        data = {
          newAddress: { ...state.address.newAddress, [name]: value }, // Cp nht trng trong newAddress
        };
      } else {
        // i vi cc form khc (profile, password)
        data = { [name]: value }; // Cp nht trc tip trng d liu
      }

      // Dispatch action  cp nht state ca form tng ng
      dispatch({
        type: ACTION_TYPES.UPDATE_FORM_STATE,
        payload: { formType, data },
      });
    },
    [state.address.newAddress] // Dependency: state.address.newAddress  m bo newAddress lun mi nht
  );

  /**
   * X l vic submit form cp nht thng tin c nhn.
   * @param {Event} e S kin submit form.
   */
  const handleSubmitProfileUpdate = (e) => {
    e.preventDefault(); // Ngn chn hnh vi mc nh ca form (ti li trang)

    // Chun b d liu cp nht, loi b khong trng tha  email v phone
    const updatedData = {
      email: state.profile.email.trim(),
      phone: state.profile.phone.trim(),
    };

    // Gi hm cp nht ngi dng v context
    const { success, message: updateMessage } = updateUserAndContext(updatedData);

    // Dispatch action  t thng bo kt qu
    dispatch({
      type: ACTION_TYPES.SET_MESSAGE,
      payload: {
        formType: FORM_TYPES.PROFILE,
        message: success ? MESSAGES.PROFILE_UPDATE_SUCCESS : (updateMessage || MESSAGES.PROFILE_UPDATE_FAILED),
      },
    });
  };

  /**
   * X l vic submit form i mt khu.
   * @param {Event} e S kin submit form.
   */
  const handleSubmitPasswordChange = (e) => {
    e.preventDefault(); // Ngn chn hnh vi mc nh

    // c danh sch ngi dng t localStorage
    const storedUsers = readUsersFromStorage();
    // Nu khng c c hoc khng c thng tin user hin ti
    if (!storedUsers || !user?.username) {
      dispatch({
        type: ACTION_TYPES.SET_MESSAGE,
        payload: { formType: FORM_TYPES.PASSWORD, message: MESSAGES.SYSTEM_ERROR_READING_USERS },
      });
      return; // Kt thc sm
    }

    // Tm ngi dng hin ti trong danh sch  c
    const currentUser = storedUsers.find((u) => u.username === user.username);
    // Nu khng tm thy
    if (!currentUser) {
      dispatch({
        type: ACTION_TYPES.SET_MESSAGE,
        payload: { formType: FORM_TYPES.PASSWORD, message: MESSAGES.USER_NOT_FOUND },
      });
      return; // Kt thc sm
    }

    // Kim tra tnh hp l ca cc trng mt khu
    const validationError = validatePasswordChange(state.password, currentUser.password);
    // Nu c li validation
    if (validationError) {
      dispatch({
        type: ACTION_TYPES.SET_MESSAGE,
        payload: { formType: FORM_TYPES.PASSWORD, message: validationError },
      });
      return; // Kt thc sm
    }

    // Nu khng c li, tin hnh cp nht mt khu
    const { success, message: updateMessage } = updateUserAndContext({ password: state.password.newPassword });
    // Nu cp nht thnh cng
    if (success) {
      // Dispatch action  reset form mt khu v hin th thng bo thnh cng
      dispatch({
        type: ACTION_TYPES.RESET_FORM,
        payload: {
          formType: FORM_TYPES.PASSWORD,
          defaultData: { oldPassword: "", newPassword: "", confirmNewPassword: "" }, // D liu mc nh sau khi reset
          message: MESSAGES.PASSWORD_CHANGE_SUCCESS,
        },
      });
      // ng xut ngi dng sau mt khong thi gian ngn
      setTimeout(logout, LOGOUT_DELAY);
    } else {
      // Nu cp nht tht bi, hin th thng bo li
      dispatch({
        type: ACTION_TYPES.SET_MESSAGE,
        payload: { formType: FORM_TYPES.PASSWORD, message: updateMessage || MESSAGES.PASSWORD_CHANGE_FAILED },
      });
    }
  };

  /**
   * X l vic thm mt a ch mi.
   * @param {Event} e S kin submit form.
   */
  const handleAddAddress = (e) => {
    e.preventDefault(); // Ngn chn hnh vi mc nh

    // Kim tra tnh hp l ca d liu a ch mi
    const validationError = validateAddress(state.address.newAddress);
    // Nu c li validation
    if (validationError) {
      dispatch({
        type: ACTION_TYPES.SET_MESSAGE,
        payload: { formType: FORM_TYPES.ADDRESS, message: validationError },
      });
      return; // Kt thc sm
    }

    // To i tng a ch mi vi ID duy nht (s dng Date.now() l cch n gin)
    // v loi b khong trng tha
    const newAddressEntry = {
      id: Date.now(), // To ID n gin, trong ng dng thc t c th cn UUID
      ...state.address.newAddress,
      address: state.address.newAddress.address.trim(),
      name: state.address.newAddress.name.trim(),
      phone: state.address.newAddress.phone.trim(),
    };

    // To mng a ch mi bng cch thm a ch va to vo danh sch hin ti
    const updatedAddresses = [...state.address.addresses, newAddressEntry];
    // Cp nht danh sch a ch trong localStorage v context
    const { success, message: updateMessage } = updateUserAndContext({ addresses: updatedAddresses });

    // Nu cp nht thnh cng
    if (success) {
      // Dispatch action  reset form thm a ch v hin th thng bo thnh cng
      dispatch({
        type: ACTION_TYPES.RESET_FORM,
        payload: {
          formType: FORM_TYPES.ADDRESS,
          defaultData: {
            addresses: updatedAddresses, // Cp nht danh sch a ch trong state
            newAddress: { address: "", name: "", phone: "" }, // Reset form thm mi
          },
          message: MESSAGES.ADDRESS_SAVE_SUCCESS,
        },
      });
    } else {
      // Nu cp nht tht bi, hin th thng bo li
      dispatch({
        type: ACTION_TYPES.SET_MESSAGE,
        payload: { formType: FORM_TYPES.ADDRESS, message: updateMessage || MESSAGES.ADDRESS_SAVE_FAILED },
      });
    }
  };

  /**
   * X l vic xa mt a ch.
   * @param {number|string} addressId ID ca a ch cn xa.
   */
  const handleDeleteAddress = (addressId) => {
    // Yu cu xc nhn t ngi dng trc khi xa.
    // Lu : window.confirm() cng c th b chn hoc khng ph hp trong mi ng cnh.
    // Cn nhc s dng modal xc nhn ty chnh.
    if (!window.confirm("Bn c chc mun xa a ch ny?")) {
      return; // Nu ngi dng khng ng , khng lm g c
    }

    // To mng a ch mi bng cch lc b a ch c ID trng khp
    const updatedAddresses = state.address.addresses.filter((addr) => addr.id !== addressId);
    // Cp nht danh sch a ch trong localStorage v context
    const { success, message: updateMessage } = updateUserAndContext({ addresses: updatedAddresses });

    // Dispatch action  cp nht state ca form a ch v hin th thng bo
    // Ngay c khi tht bi, message s c hin th, nhng addresses s khng thay i nu success l false.
    dispatch({
      type: ACTION_TYPES.UPDATE_FORM_STATE, // Hoc SET_MESSAGE nu ch mun set message v addresses ring
      payload: {
        formType: FORM_TYPES.ADDRESS,
        data: {
          // Ch cp nht addresses trong state nu lu vo localStorage thnh cng
          addresses: success ? updatedAddresses : state.address.addresses,
          // t message tng ng
          message: success ? MESSAGES.ADDRESS_DELETE_SUCCESS : (updateMessage || MESSAGES.ADDRESS_SAVE_FAILED), // Gi s li lu l chung
        },
      },
    });
  };


  // --- Phn render ca component ---

  // Nu ngi dng cha ng nhp hoc khng c thng tin user, khng render g c ( c x l  useEffect)
  // Tuy nhin, thm mt kim tra  y  m bo an ton.
  if (!isLoggedIn || !user?.username) {
    // useEffect  x l iu hng, nhng tr v null  y  trnh li render.
    return null;
  }

  // Tr v JSX  render giao din ngi dng
  return (
    <div className="user-profile-container"> {/* Container chnh ca trang h s */}
      <h1>Xin cho, {user.username}!</h1> {/* Li cho vi tn ngi dng */}
      <p>Qun l thng tin v n hng ca bn.</p> {/* M t ngn */}

      {/* Menu iu hng gia cc mc ca trang h s */}
      <nav className="profile-sections-menu">
        {SECTIONS.map((section) => ( // Lp qua mng SECTIONS  to cc nt
          <button
            key={section.id} // Key cho mi nt
            className={state.activeSection === section.id ? "active" : ""} // Class "active" nu mc ang c chn
            onClick={() => dispatch({ type: ACTION_TYPES.SET_ACTIVE_SECTION, payload: section.id })} // Dispatch action khi click
          >
            {section.label} {/* Nhn ca nt */}
          </button>
        ))}
      </nav>

      {/* Hin th form tng ng vi mc ang c chn (activeSection) */}
      {/* Mc Thng tin c nhn */}
      {state.activeSection === FORM_TYPES.PROFILE && (
        <ProfileForm
          formData={state.profile} // D liu cho form
          onChange={(e) => handleInputChange(e, FORM_TYPES.PROFILE)} // Hm x l thay i input
          onSubmit={handleSubmitProfileUpdate} // Hm x l submit form
          message={state.profile.message} // Thng bo ca form
        />
      )}

      {/* Mc i mt khu */}
      {state.activeSection === FORM_TYPES.PASSWORD && (
        <PasswordForm
          formData={state.password} // D liu cho form
          onChange={(e) => handleInputChange(e, FORM_TYPES.PASSWORD)} // Hm x l thay i input
          onSubmit={handleSubmitPasswordChange} // Hm x l submit form
          message={state.password.message} // Thng bo ca form
        />
      )}

      {/* Mc a ch giao hng - Lu : section.id trong SECTIONS l "addresses" */}
      {state.activeSection === "addresses" && ( // So snh vi 'addresses' nh trong SECTIONS
        <AddressForm
          addresses={state.address.addresses} // Danh sch a ch
          newAddressFormData={state.address.newAddress} // D liu form thm a ch mi
          onChange={(e) => handleInputChange(e, FORM_TYPES.ADDRESS)} // Hm x l thay i input
          onAddAddress={handleAddAddress} // Hm x l thm a ch
          onDeleteAddress={handleDeleteAddress} // Hm x l xa a ch
          message={state.address.message} // Thng bo ca form
        />
      )}

      {/* Mc Lch s n hng - Lu : section.id trong SECTIONS l "orders" */}
      {state.activeSection === "orders" && ( // So snh vi 'orders' nh trong SECTIONS
        <section className="order-history-section"> {/* Container cho lch s n hng */}
          <h2>Lch s n hng</h2> {/* Tiu  */}
          <OrderHistory /> {/* Component hin th lch s n hng */}
        </section>
      )}

      {/* Link  quay li ca hng */}
      <div className="back-link">
        <Link to="/home"> Quay li ca hng</Link> {/* S dng Link ca react-router-dom */}
      </div>
    </div>
  );
};

// Xut component UserProfilePage lm default export
export default UserProfilePage;

