import React, { createContext, useState, useEffect, useCallback } from "react";

// --- HNG S ---
// Kha lu tr thng tin ngi dng trong localStorage
const LOCAL_STORAGE_KEY = "currentUser";

// Gi tr mc nh cho Context
const DEFAULT_AUTH_CONTEXT = {
  isLoggedIn: false,
  user: null,
  login: () => {},
  logout: () => {},
};

// To Context
export const AuthContext = createContext(DEFAULT_AUTH_CONTEXT);

// --- HM TIN CH ---

/**
 * Ly thng tin ngi dng t localStorage
 * @returns {Object|null} Thng tin ngi dng hoc null nu khng hp l
 */
const getStoredUser = () => {
  try {
    const savedUser = localStorage.getItem(LOCAL_STORAGE_KEY);
    if (savedUser) {
      const parsedUser = JSON.parse(savedUser);
      if (parsedUser && typeof parsedUser === "object" && parsedUser.username) {
        return {
          ...parsedUser,
          email: parsedUser.email || "", // m bo email lun tn ti
        };
      }
      console.warn("D liu ngi dng trong localStorage khng hp l,  xa.");
      localStorage.removeItem(LOCAL_STORAGE_KEY);
    }
    return null;
  } catch (error) {
    console.error("Li khi c d liu ngi dng t localStorage:", error);
    localStorage.removeItem(LOCAL_STORAGE_KEY);
    return null;
  }
};

/**
 * Lu thng tin ngi dng vo localStorage
 * @param {Object} userData - D liu ngi dng
 */
const saveUserToStorage = (userData) => {
  try {
    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(userData));
  } catch (error) {
    console.error("Li khi lu thng tin ngi dng vo localStorage:", error);
  }
};

/**
 * Xa thng tin ngi dng khi localStorage
 */
const clearUserFromStorage = () => {
  try {
    localStorage.removeItem(LOCAL_STORAGE_KEY);
  } catch (error) {
    console.error("Li khi xa thng tin ngi dng khi localStorage:", error);
  }
};

// --- THNH PHN CHNH ---

/**
 * Provider cho qun l trng thi xc thc ngi dng
 * @param {Object} props - Props cha children
 * @returns {JSX.Element} Component Provider
 */
const AuthProvider = ({ children }) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [user, setUser] = useState(null);

  /**
   * Khi to trng thi ng nhp t localStorage khi component mount
   */
  useEffect(() => {
    const storedUser = getStoredUser();
    if (storedUser) {
      setIsLoggedIn(true);
      setUser(storedUser);
    }
  }, []);

  /**
   * X l ng nhp vi d liu ngi dng
   * @param {Object} userData - D liu ngi dng (t nht phi c username)
   */
  const login = useCallback((userData) => {
    if (!userData || typeof userData !== "object" || !userData.username) {
      console.error("D liu ngi dng khng hp l:", userData);
      return;
    }
    const normalizedUserData = {
      ...userData,
      email: userData.email || "", // m bo email lun tn ti
    };
    saveUserToStorage(normalizedUserData);
    setIsLoggedIn(true);
    setUser(normalizedUserData);
  }, []);

  /**
   * X l ng xut
   */
  const logout = useCallback(() => {
    clearUserFromStorage();
    setIsLoggedIn(false);
    setUser(null);
  }, []);

  // Gi tr Context
  const authContextValue = {
    isLoggedIn,
    user,
    login,
    logout,
  };

  return <AuthContext.Provider value={authContextValue}>{children}</AuthContext.Provider>;
};

export default AuthProvider;